<template><section><h2>RecycleList </h2>
<p> DOM </p>
<h3></h3>
<ul>
<li>
<p></p>
</li>
<li>
<p></p>
</li>
<li>
<p> Promise Promise polyfill </p>
</li>
</ul>
<h3></h3>
<div class="demo-block mt_15" style="height: 400px">
<tj-recycle-list class="list" :size="size" :on-fetch="onFetch" :offset="offset">
  <template slot="item" slot-scope="{ data }">
    <tj-row flex class="mt_15" :gutter="15" @click="handleClick(data)">
      <tj-col width="48px">
        <img width="100%" :src="data.avatar">
      </tj-col>
      <tj-col class="bubble">
        <p>{{ data.msg }}</p>
        <div class="meta">
          <time class="posted-date">{{ data.time }}</time>
        </div>
      </tj-col>
    </tj-row>
  </template>
</tj-recycle-list>

</div>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>infinite</td>
<td></td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td></td>
<td>number</td>
<td></td>
<td>20</td>
</tr>
<tr>
<td>offset</td>
<td></td>
<td>number</td>
<td></td>
<td>200</td>
</tr>
<tr>
<td>onFetch</td>
<td></td>
<td>function</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>item</td>
<td></td>
</tr>
<tr>
<td>noMore</td>
<td></td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      size: 50,
      offset: 100
    }
  },
  methods: {
    onFetch() {
      let items = []
      return new Promise((resolve) => {
        //  50  size  50
        setTimeout(() => {
          for (let i = 0; i < 50; i++) {
            items.push({
              id: i,
              avatar: 'https://s3.amazonaws.com/uifaces/faces/twitter/danpliego/128.jpg',
              msg: 'name' + i,
              time: 'Thu Oct 25 2018 15:02:12 GMT+0800 ()'
            })
          }
          resolve(items)
        }, 1000)
      })
    },
    handleClick(data) {
      console.log('Item:' + data)
    }
  }
}
</script>


